@import "../abstracts/variables";

.button {
	display: inline-block;
	height: auto;
	padding: 0.8rem 2rem;
	border: none;
	border-radius: 6px;
	font-weight: 500;
	font-size: 1.6rem;
	text-decoration: none;
	text-transform: capitalize;
	cursor: pointer;
	overflow: hidden;
	&__select {
		color: $black-2;
		font-family: Poppins;
		padding: 1rem;
		border: none;
		background-color: $bg-3;

		width: 150px;
		cursor: pointer;
	}
}
.primary {
	background-color: $primaryPurple;
	color: $white;
}
.secondary {
	background-color: $bg-3;
	color: $black-1;
}